*
  margin: 0
  padding: 0

html
  box-sizing: border-box

  *
    box-sizing: inherit

    &:before,
    &:after
      box-sizing: border-box

html,
body
  -webkit-text-size-adjust: 100%
  width: 100%
  height: 100%
  color: $base-text-color
  font-family: -apple-system, BlinkMacSystemFont, "游ゴシック", YuGothic, "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif
  font-size: 62.5%
  font-weight: 500
  line-height: 1

article,
aside,
figure,
footer,
header,
section
  display: block

h1,
h2,
h3,
h4,
h5,
h6
  font-size: 100%
  font-weight: normal

ul
  list-style: none

img
  max-width: 100%
  height: auto
  vertical-align: top

table
  border-collapse: collapse
  border-spacing: 0

th,
td
  padding: 0
  text-align: left

a
  color: inherit
  text-decoration: none

//form reset
input, button, textarea, select
  margin: 0
  padding: 0
  background: none
  border: none
  border-radius: 0
  outline: none
  -webkit-appearance: none
  -moz-appearance: none
  appearance: none

iframe
  border: none
