/*==========================
           helpers
 ==========================*/

/*display
 ==========================*/
.hp-hidden-pc
  display: none !important
  +mq-sp-only
    display: block !important

.hp-hidden-sp
  +mq-sp-only
    display: none !important

.hp-hidden-tablet
  +mq-small-pc-only
    display: none !important

/*margin
 ==========================*/

@for $i from 0 through 10
  .hp-mg#{$i * 5}
    margin: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-mt#{$i * 5}
    margin-top: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-mr#{$i * 5}
    margin-right: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-mb#{$i * 5}
    margin-bottom: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-ml#{$i * 5}
    margin-left:#{$i * 5}px !important

/*padding
 ==========================*/

@for $i from 0 through 10
  .hp-pg#{$i * 5}
    padding: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-pt#{$i * 5}
    padding-top: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-pr#{$i * 5}
    padding-right: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-pb#{$i * 5}
    padding-bottom: #{$i * 5}px !important

@for $i from 0 through 10
  .hp-pl#{$i * 5}
    padding-left:#{$i * 5}px !important


/*text-align
 ==========================*/
.hp-text-left
  text-align: left !important

.hp-text-right
  text-align: right !important

.hp-text-center
  text-align: center !important

